/*
 * Modular Scale
 * https://alistapart.com/article/more-meaningful-typography
 * 1.00rem @ 1:1.5
 * 1.25rem @ 1:1.5
 * https://www.modularscale.com/?1,1.25&em&1.5
 */
.form {
  padding: 1rem 0;
  flex-grow: 1; }

.text, .options, .generate, .results {
  display: block;
  margin: 1rem 0; }

.text, .options, .results {
  width: 100%; }

.text {
  height: 7.59375rem;
  overflow-x: hidden;
  overflow-y: scroll;
  resize: vertical; }

.options label {
  margin: 0 1rem; }
  .options label:first-child {
    margin-left: 0; }
  .options label:last-child {
    margin-right: 0; }

.num {
  width: 3.375rem; }
