html, body, #root {
  height: 100%;
  max-width: 100vw;
  margin: 0;
  padding: 0; }

#root {
  width: 92%;
  max-width: 86.49756rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  display: grid;
  grid-template-rows: auto 1fr auto; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

h1, h2, h3, h4, h5, h6, p {
  padding: 0;
  margin: 0;
  font-weight: inherit; }

h1, h2, h3, h4, h5, h6 {
  font-weight: bolder;
  text-rendering: optimizeLegibility;
  margin-top: 2.8125rem;
  margin-bottom: 0.9375rem;
  color: #175963; }

h6, p, label, button, input, textarea, select, optgroup {
  font-size: 1rem; }

h6 {
  line-height: 2; }

h5 {
  font-size: 1.25rem;
  line-height: 2; }

h4 {
  font-size: 1.5rem;
  line-height: 1.66667; }

h3 {
  font-size: 1.875rem;
  line-height: 1.33333; }

h2 {
  font-size: 2.25rem;
  line-height: 1.11111; }

h1 {
  font-size: 2.8125rem;
  line-height: 0.88889; }

p, label, button, input, textarea, select, optgroup {
  line-height: 1.25rem; }

p, label {
  margin: 1.25rem 0; }
  p *:last-child, label *:last-child {
    margin-bottom: 0; }

a {
  background-color: transparent;
  outline-offset: 0.13169rem; }
  a:link, a:visited, a:hover, a:active {
    text-decoration: underline; }
  a:link, a:visited {
    color: #175963; }
  a:hover {
    color: #0d7471; }
  a:active {
    color: #215259; }

button, input, textarea, select, optgroup {
  font: inherit; }

button {
  cursor: pointer;
  color: white;
  background: none;
  background-color: #0d7471;
  border: 1px solid rgba(20, 78, 87, 0.25);
  margin: 0;
  padding: 0.66667rem 1rem;
  box-shadow: 0 0.2963rem 0.37037rem 0 rgba(0, 0, 0, 0.2), 0 0.44444rem 1.25rem 0 rgba(0, 0, 0, 0.19); }
  button:hover {
    background-color: #0b6664; }
  button:active {
    background-color: #0a5856; }

::-webkit-scrollbar {
  width: 0.83333rem; }

::-webkit-scrollbar-track {
  background-color: transparent; }

::-webkit-scrollbar-thumb {
  background-color: rgba(23, 89, 99, 0.8); }
  ::-webkit-scrollbar-thumb:hover {
    background-color: rgba(20, 78, 87, 0.8); }
